var utils = require('./utils');

class component {
    constructor(domNode,cleanDom = true) {
        this.$tpl = domNode.outerHTML.replace(/[\r\n]/g, "");
        this.initFactory();

        if(cleanDom){
            domNode.parentNode.removeChild(domNode);
        }
    };


    initFactory() {
        var _this = 123;
        var keywords = ['if', 'for', 'else'].map(function (item) {
            return '\\b(' + item + ')\\b';
        });
        var regexp = new RegExp(keywords.join("|"));
        var code = 'this.$factory = (function($data,uuid){ if(!$data){$data = {};} if(uuid == null){ uuid = faker.set($data);}  var $current = "faker.get(\'"+uuid+"\')"; var $last = $current; var buffer = []; buffer.push(\'';
        var count = 0;
        var target = -1;

        var code2 = this.$tpl.replace(/\{\{([\s\S]+?)\}\}|[^\{\{]+/g, function (matched, a) {

            if (!a) {
                return matched
                    .replace(/[\\"']/g, '\\$&')
                    .replace(/\u0000/g, '\\0');
            }
            else {
                a = utils.htmlspecialchars_decode(a.trim());
            }

            var _matched;

            if (a == 'end' || a == '}') {
                count--;
                if(count == target){
                    target = -1;
                    return '\');' + '} $current = $last;' + " buffer.push(\'";
                }
                else{
                    return '\');' + '}' + " buffer.push(\'";
                }
            }
            else if (_matched = a.match(/for\s*(\w+)(?:,\s*(\w+))?\s*in\s*([^\}\{]+)/)) {
                if (!_matched[2]) {
                    _matched[2] = '_index';
                }
                target = count;
                return '\');' + ` for(var ${_matched[2]} in ${_matched[3]}){ var ${_matched[1]} = ${_matched[3]}[${_matched[2]}]; $last = $current; $current += "[" + ${_matched[2]} + "]"; if(typeof ${_matched[1]} == 'function'){ continue; } ` + " buffer.push(\'";
                //console.log(_matched)
            }
            else if (_matched = a.match(regexp)) {
                switch (_matched[0]) {
                    case 'for':
                    case 'if':
                        count++;
                        return '\');' + a + "{ buffer.push(\'";
                    case 'else':
                        return '\'); }' + a + "{ buffer.push(\'";

                }
                return '\');' + a + " buffer.push(\'";
            }
            else {
                return '\'); buffer.push(' + a + ');buffer.push(\'';
            }
            /*
            if(/^for\b/.test(a) || ){

            }
            if (a.indexOf('for') > -1 || a.indexOf('}') > -1) {
                return '\');' + a + " buffer.push(\'";
            }
            else {
                
            }
            */
        });

        var finalCode = code + code2 + '\'); var ret = buffer.join(""); ret = ret.replace(/^(<\\S+)/,"$1 data-uuid=" + uuid); return ret; });';
        eval(finalCode);

    };

    
    //初始化一个组件实例
    getWidget() {

    };




}

module.exports = component;


