const component = require("./component");
const widget = require("./widget");
const domready = require('./domready');
const utils = require('./utils');

var components = {};
var data = {};

var faker = {

    scanComponent: function (domNode) {
        var widgetName = domNode.getAttribute('f-widget');
        if (widgetName == '' || widgetName == null) {
            return;
        }
        if (components[widgetName]) {
            return;
        }
        components[widgetName] = new component(domNode);
    },

    widget: function (componentName, data = {}) {
        if (!components[componentName]) {
            return '';
        }
        return components[componentName].$factory(data);
    },
    set: function (obj) {
        var uuid = utils.uniqid('obj_');
        data[uuid] = obj;
        // data[obj] = uuid;
        // data[uuid] = obj;
        return uuid;
    },

    remove: function (uuid) {
        delete data[uuid];
        // for (var [key, uuid] of data) {
        // if (uuid == obj) {
        // data.delete(key)
        // }
        // }

    },

    setValue: function (elem, value) {
        if (!elem || elem.name == '' || elem.name == null) {
            return;
        }
        var obj = faker.get(elem);
        if (obj) {
            var indexs = elem.name.match(/[^\[\]]+/g).map(function (item) {
                if (item[0] == '[') {
                    var index = item.substr(1, item.length - 1);
                }
                else {
                    var index = item;
                }
                if (index[0] != "'" && index[0] != '"') {
                    index = `"${index}"`;
                }
                return `[${index}]`;
            }).join("");

            var js = "obj" + indexs + " = \"" + value + '"';

            try {
                eval(js);
            }
            catch (e) { }
        }
    },


    get: function (obj) {
        var _uuid;
        if (utils.is(obj, "string")) {
            _uuid = obj;
        }
        else if (obj.nodeType) {
            var item = obj;
            do {
                if (!item.getAttribute) {
                    break;
                }
                var uuid;
                if (uuid = item.getAttribute('data-uuid')) {
                    _uuid = uuid;
                    break;
                }
            } while (item = item.parentNode);
        }
        if (data[_uuid]) {
            return data[_uuid];
        }
        // alert(2)
        // for (var i in data) {
        //     console.log(i);
        //     console.log(data[i]);
        // }
        // var keys = data.keys();
        // alert(keys.length)
        // for(var i = 0; i < keys.length; i++){
        //     var key = keys[i];
        //     alert(key)
        //     if(data[key] == _uuid){
        //         alert(2);

        //     }
        // }
        // for (var key in data.keys()) {
        //     if (data[key] == _uuid) {
        //         return data;
        //     }
        // }
        // console.log(keys)

        // for (var [key, uuid] of data) {
        //     if (uuid == _uuid) {
        //         return key;
        //     }
        // }
        return null;
    },

    bootstrap: function () {
        domready(function () {
            //扫描所有组件
            var elems = document.querySelectorAll("[f-widget]");
            for (var i = 0; i < elems.length; i++) {
                faker.scanComponent(elems[i]);
            }
            var elems = document.querySelectorAll("[f-tpl]");
            for (var i = 0; i < elems.length; i++) {
                (new widget(elems[i]));
            }


        });


        //双向绑定
        var timer = null;
        var handler = function (e) {
            if (timer) {
                clearTimeout(timer);
                timer = null;
            }
            setTimeout(function () {
                var elem = e.target;
                if (!elem || elem.name == '' || elem.name == null) {
                    return;
                }
                faker.setValue(elem, elem.value);
            });

            // var obj = faker.get(e.target);
            // if (obj) {
            //     var js = "obj['" + elem.name + '\'] = "' + elem.value + '"';
            //     console.log(js)
            //     eval(js);

            //     try {
            //     }
            //     catch (e) { }
            // }
        };
        if (document.addEventListener) {
            document.addEventListener('input', handler);
            document.addEventListener('change', handler);
        }


    },
    showData: function () {
        console.log(data)
    }



};

global.faker = faker;



