const proxy = require("./proxy");
const utils = require('./utils');
const htmlParserLibrary = require('html-parser-lite');
const htmlParser = new htmlParserLibrary();
const svd = require('simple-virtual-dom')
const component = require('./component');
const el = svd.el;
const diff = svd.diff
const patch = svd.patch




class widget {
    constructor(domNode,_component = null) {
        this.$timer = null;
        this.$element = domNode;
        if(_component == null){
            this.$prototype = new component(domNode,false);
        }
        else{
            this.$prototype = _component;
        }
        //this.$tpl = domNode.outerHTML.replace(/[\r\n]/g, "");
        var data = null;
        var _data = domNode.getAttribute('f-tpl');
        //如果没有声明被绑定的对象，视为一般的模板，不再响应双向绑定
        if(_data == ''){
            this.$proxy = proxy({},this);       
        }
        else{
            //如果已经存在这个对象，那么直接使用
            if (!window[_data]) {
                window[_data] = {}
            }
            window[_data] = this.$proxy = proxy(window[_data],this);
        }

        this.$uuid = faker.set(this.$proxy);
        this.render();

    };


    render(){

        if(this.$timer){
            clearTimeout(this.$timer);
            this.$timer = null;
        }
        this.$timer = setTimeout(() => {
            var html = this.$prototype.$factory(this.$proxy,this.$uuid);

            //build virtual dom
            var vdom = this.convertVirtualDom(html);
            //
            if(this.$vdom){
                //二次渲染的时候，移除没用的内容
                var elems = this.$element.querySelectorAll('[data-uuid]');
                for(var i = 0; i < elems.length; i++){
                    var uuid = elems[i].getAttribute('uuid');
                    faker.remove(uuid);
                }
                var patches = diff(this.$vdom,vdom);
                patch(this.$element,patches);
            }
            else{
                var realDom = vdom.render();
                this.$element.parentNode.replaceChild(realDom,this.$element);
                this.$element = realDom;
            }
            this.$vdom = vdom;

        },0);
     
    };

    parseHTML(html){
        var root = htmlParser.parse(html);
        return root;
    };

    convertVirtualDom(html){
        var vm = this.parseHTML(html);
        var node = vm.childNodes[0];  
        return el(node.tagName,node.attrs,this.generateVirtualDomNode(node.childNodes));
    };

    generateVirtualDomNode(nodes){
        var ret = [];
        for(var i = 0; i < nodes.length; i++){
            var node = nodes[i];
            if(node.nodeType == 1){
                //修正input显示“”的问题
                if(node.tagName === 'input' && node.attrs.value == '""'){
                    node.attrs.value = "";
                }
                ret.push(el(node.tagName,node.attrs,this.generateVirtualDomNode(node.childNodes)));
            }
            else if(node.nodeType == 3){
                ret.push(node.textContent);
            }
        }
        return ret;
    };
}

module.exports = widget;




